.big {
  width: 100%;
  height: 100vh;
  background-image: url("https://talent.socialhackersacademy.org/wp-content/uploads/2020/10/talent_2_bg.png") !important;
}
.forms-table {
  margin: 0 auto;
  margin-top: 50px;
  width: 70%;
  height: 50vh;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 1em;
  justify-items: center;
  align-items: center;
}

.remake {
  background-color: #004688;
}

.act {
  color: #004688;
}
.custom-table {
  margin: 0 auto;
  margin-top: 50px;
  width: 70%;
  height: 50vh;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 1em;
  justify-items: center;
  align-items: center;
}
.custom-table div,
.custom-table div {
  height: 100px;
  display: grid;
  grid-template-rows: 3fr 1fr;
}

.lii {
  cursor: pointer;
}
